@import "colors";
/*-------------------------------------------------- header --------------------------------------------------*/

.main_visual_typo {
  display: none;
}

.main_title {
  display: block;
}

/*-------------------------------------------------- Activities --------------------------------------------------*/

.activities {
  display: flex;
  position: relative;
  height: auto;
  margin: 0;
  color: $white;
}

.activities_container {
  color: $black;
  display: block;
  width: 670px;
}

/*-------------------------------------------------- ActivitiesList --------------------------------------------------*/

.activities_list_container {
  width: 670px;
  margin-top: 70px;
  border-bottom: 1px solid $border-gray;
}

.activities_list_image_box {
  width: 670px;
}

.activities_list_image {
  width: 100%;
}

.activities_list_info {
  margin-top: 20px;
  display: flex;
}

.activities_list_date {
  height: 15px;
  padding-right: 15px;
  color: $activity-text-light;
  font-size: 12px;
  border-right: 1px solid $activity-text-light;
  box-sizing: border-box;
}

.activities_list_date p {
  padding: 0;
  margin: 0;
}

.activities_list_category {
  height: 15px;
  padding-left: 15px;
  color: $activity-text-light;
  font-size: 12px;
  line-height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.activities_list_category_item {
  text-decoration: none;
}

.activities_list_title {
  font-size: 16px;
  font-weight: bold;
  text-align: left;
  text-decoration: none;
}

.activities_list_content {
  color: black;
  text-align: left;
  margin-top: 15px;
}

.read_more_button {
  text-align: center;
  text-decoration: none;
  color: $activity-text-gray;
}

/*-------------------------------------------------- ActivitiesItem --------------------------------------------------*/

.v_item_container {
  width: 670px;
  margin-top: 70px;
  border-bottom: 1px solid $border-gray;
}

.activities_item_image_box {
  margin: 60px 0 60px 0;
  width: 670px;
}

.activities_item_image {
  width: 100%;
}

.activities_item_info {
  margin-top: 20px;
  display: flex;
}

.activities_item_date {
  height: 15px;
  padding-right: 15px;
  color: $activity-text-light;
  font-size: 12px;
  border-right: 1px solid $activity-text-light;
  box-sizing: border-box;
}

.activities_item_date p {
  padding: 0;
  margin: 0;
}

.activities_item_title {
  font-size: 18px;
  font-weight: bold;
  text-align: left;
  text-decoration: none;
}

.activities_item_category {
  height: 15px;
  padding-left: 15px;
  color: $activity-text-light;
  font-size: 12px;
  line-height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.activities_item_category_item {
  text-decoration: none;
}

.activities_item_content {
  color: $black;
  text-align: left;
  font-size: 14px;
}

.read_more_button {
  text-align: center;
  text-decoration: none;
  color: $activity-text-gray;
}

.activities_item_tags {
  margin: 60px 0 30px 0;
  color: $black;
  font-size: 12px;
}

.activities_item_tags_title {
  font-weight: bold;
}

.activities_item_tags_item {
  text-decoration: none;
}

/*-------------------------------------------------- Sidebar --------------------------------------------------*/

.sidebar_activities {
  margin: 70px 0 0 50px;
  width: 300px;
  color: $black;
}

.sidebar_content_title {
  position: relative;
  margin-bottom: 48px;
  color: $activity-text-dark;
}

.sidebar_content_title::after {
  background-color: $primary-red;
  content: '';
  height: 1px;
  width: 40px;
  position: absolute;
  left: 0;
  bottom: 0;
  margin-bottom: -20px;
}

.popular_posts {
  margin-bottom: 75px;
}

.categories {
  margin-bottom: 75px;
}

.category_item {
  font-size: 12px;
  display: block;
  margin-bottom: 15px;
  text-decoration: none;
  color: $activity-text-light;
}

.archive {
  margin-bottom: 75px;
}

.archives_list {
  padding: 0;
  font-size: 12px;
  color: $activity-text-dark;
}

.archives_year {
  margin-bottom: 20px;
}

.archives_month_list_box {
  display: inline-block;
    margin: 0 15px 20px 0;
}

.archives_month_list {
  list-style: none;
}

.archives_month_item {

}

.archives_month_item a {
  color: $activity-text-dark;
  text-decoration: none;
}

.tags {
  margin-bottom: 75px;
}

.tag_item {
  font-size: 12px;
  border: solid 1px $border-gray;
  padding: 8px;
  display: inline-block;
  margin: 0 10px 10px 0;
  text-decoration: none;
  color: $activity-text-gray;
}

/*-------------------------------------------------- pagination --------------------------------------------------*/

.pages_link {
  margin: 80px 0 80px 0;
  top: 50%;
  left: 50%;
}

.next_after_pagination {
  margin-bottom: 80px;
}
